import { keyboard } from "@testing-library/user-event/dist/keyboard";
import React, { useEffect } from "react";
import { Block } from "./components/block";

const App = () => {
    let arr = []
    let textOfClickedValue

    const [forReload, setForReload] = React.useState(0);
    const [rates, setRates] = React.useState();

    // код валюты
    const [fromCurrency, setFromCurrency] = React.useState();
    const [toCurrency, setToCurrency] = React.useState();

    const [final, setFinal] = React.useState(false)

    // для поиска
    const [fromSearch, setFromSearch] = React.useState('')
    const [toSearch, setToSearch] = React.useState('')

    // открытие закрытие меню
    const [fromOpened, setFromOpened] = React.useState(false);
    const [toOpened, setToOpened] = React.useState(false);

    // курс на кликнутой валюте
    const [toValue, setToValue] = React.useState(0)
    const [fromValue, setFromValue] = React.useState(0)

    // выпадающие листы
    const [fromListRate, setFromListRate] = React.useState(['RUB', 'USD', 'EUR'])
    const [toListRate, setToListRate] = React.useState(['RUB', 'USD', 'EUR'])

    // для конвентатора 
    const [fromConventor, setfromConventor] = React.useState()
    const [toConventor, settoConventor] = React.useState()


    // fetch
    useEffect(() => {
        fetch('https://api.currencyapi.com/v3/latest?apikey=TACTpWYpPjh6zIk8adZrvAjlJFZ0aiN25snJQ8EN')
            .then(res => res.json())
            .then(data => {
                newArr(data.data)
            })
            .finally(() => setFinal(true))
    }, [])

    // get array
    const newArr = (data) => {
        for (var elem in data) {
            arr = ([...arr, data[elem]]);
        }
        setRates(arr)

    }

    // получение курса
    const fromClickValue = (event) => {

        textOfClickedValue = event.code;

        if (textOfClickedValue === fromListRate[0] || textOfClickedValue === fromListRate[1]) {
            return
        }

        fromListRate.length = 2
        fromListRate.unshift(textOfClickedValue)

        setFromCurrency(event.code)
        setFromValue(event.value)

        setfromConventor('')
        settoConventor('')

    }
    // получение курса   
    const toClickValue = (event) => {

        textOfClickedValue = event.code;

        if (textOfClickedValue === toListRate[0] || textOfClickedValue === toListRate[1]) {
            return
        }

        toListRate.length = 2
        toListRate.unshift(textOfClickedValue)

        setToCurrency(event.code)
        setToValue(event.value)

        setfromConventor('')
        settoConventor('')
    }


    // записываем значения для поиска
    const inputFromValues = (event) => {

        setFromSearch(event.target.value)

    }
    const inputToValues = (event) => {

        setToSearch(event.target.value)

    }


    // открытие закрытие выпадающего меню
    const openFromListOfValues = (event) => {


        if (event.target.type === 'text') {
            return
        } else {
            setFromOpened(!fromOpened)
            console.log(fromOpened, 'from');
        }
    }
    const openToListOfValues = (event) => {
        if (event.target.type === 'text') {
            return
        } else {
            setToOpened(!toOpened)
            console.log(toOpened, 'to');
        }
    }

    // установление курса
    const onSetFromCurrency = (el) => {
        setFromCurrency(el)
        console.log(el);

        rates.map((element) => {
            if (element.code === el) {
                setFromValue(element.value)
            }
        })


        setfromConventor('')
        settoConventor('')
    }
    const onSetToCurrency = (el) => {

        setToCurrency(el)
        rates.map((element) => {
            if (element.code === el) {
                setToValue(element.value)
            }
        })

        setfromConventor('')
        settoConventor('')
    }


    const fromConventror = (event) => {

        if (fromCurrency == null || toCurrency == null) {
            alert('choose a currency')
            event.target.value = ''
        }

        const fromNumber = event.target.value
        setfromConventor(fromNumber)
        settoConventor(parseFloat((toValue / fromValue * fromNumber).toFixed(3)))

        if (event.target.value == '') {
            settoConventor('')
        }


    }
    const toConventror = (event) => {

        if (fromCurrency == null || toCurrency == null) {
            alert('choose a currency')
            event.target.value = ''
        }

        const toNumber = event.target.value
        settoConventor(toNumber)
        setfromConventor(parseFloat((fromValue / toValue * toNumber).toFixed(5)))

        if (event.target.value == '') {
            setfromConventor('')
        }
    }


    return (

        <div>
            {final ? (
                <div className="wrapper">
                    <Block
                        rates={rates}
                        currency={fromCurrency}
                        onChangeCurrency={onSetFromCurrency}
                        clickValue={fromClickValue}
                        listRate={fromListRate}
                        inputValues={inputFromValues}
                        openListOfValues={openFromListOfValues}
                        opened={fromOpened}
                        search={fromSearch}
                        conventor={fromConventror}
                        conventorValue={fromConventor} />


                    <Block
                        rates={rates}
                        currency={toCurrency}
                        onChangeCurrency={onSetToCurrency}
                        clickValue={toClickValue}
                        listRate={toListRate}
                        inputValues={inputToValues}
                        openListOfValues={openToListOfValues}
                        opened={toOpened}
                        search={toSearch}
                        conventor={toConventror}
                        conventorValue={toConventor} />

                </div>
            ) : (

                <div>
                    <h1>Зогружаюсь, подаждите пожолуйста</h1>
                </div>

            )
            }
        </div>
    )
}

export default App

