import React, { useEffect } from "react";

export const Block = ({ rates,
    currency,
    onChangeCurrency,
    openList, clickValue,
    listRate,
    inputValues,
    search,
    openListOfValues,
    opened,
    conventor,
    conventorValue
}) => {

    const historyRates = [listRate[0], listRate[1], listRate[2]]



    return (
        <div className="block">

            <div className="block__rates rates-block">
                <ul className="rates-block__list">

                    {historyRates.map((el, index) => (
                        <li
                            onClick={() => onChangeCurrency(el)}
                            key={index}
                            className={`rates-block__item ${currency === el ? 'active' : ''}`}>{el}</li>
                    ))}


                    <li onClick={openListOfValues} className={`rates-block__item values`}>

                        <ul className={`values__list ${opened ? 'block' : ''}`}>

                            <input onChange={inputValues} value={search}
                                type="text"
                                className="values__input" />


                            {rates.filter((item) => {

                                const name = item.code.toLowerCase()
                                return name.includes(search.toLowerCase())

                            }).map((elem, index) => (
                                <li onClick={() => clickValue(elem)} key={index} className="values__item">{elem.code}</li>
                            ))}

                        </ul>

                    </li>

                </ul>
            </div >
            <div className="block__input input-block">
                <input type="number" className="input-block__item" value={conventorValue} onChange={conventor} />
            </div>
        </div >
    )
}

